.App {
  text-align: center;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.question {
  font-size: 20px;
  margin-bottom: 10px;
  text-align: left;
}

.options {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
}

.option {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid transparent; /* Removido o estilo de borda */
  cursor: pointer;
  width: 100%;
  border-radius: 8px;
  user-select: none;
  transition: border-color 0.3s; /* Adicionada transição para a cor da borda */
}

.option:hover {
  border-color: green; /* Adicionado estilo de borda verde ao passar o mouse */
}

.option.selected {
  border-color: green; /* Adicionado estilo de borda verde para a opção selecionada */
}

.result {
  font-size: 24px;
  font-weight: bold;
  margin-top: 20px;
  text-align: left;
}

.navigation-buttons {
  margin-top: 20px;
}
